
.users {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 40px;
    padding: 20px;
}


.info {
    width: 400px;
    height: 80px;
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 2px solid gray;
    border-radius: 10px;
}


.info > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.info span {
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
}


.info p {
    font-size: 16px;
}


.avatar {
    width: 80px;
    border-radius: 50%;
}